import React, { Component } from 'react';
import 'scss/index';
import './App.scss';
import Profile from 'comp/Profile';
import Contents from 'comp/Contents';

class App extends Component {
	render() {
		return (
			<div className="bg">
				<main className="container">
					<div>
						<Profile />
					</div>
					<div>
						<Contents />
					</div>
				</main>
			</div>
		);
	}
}

export default App;
